<template>
  <div class="article">
    <div class="main">
      <div class="d-title">文章搜索</div>
      <div class="content">
        <el-collapse v-model="activeNames" @change="handleChange">
          <el-collapse-item :title="item.article_title" :name="index" v-for="(item,index) of $store.state.article_group"
            :key="item.id">
            <div>{{item.article_tabloid}}</div>
            <div class="update-date">时间：{{item.update_date}}</div>
            <div class="read-btn">
              <el-button size="mini" type="primary" @click="gotoDetails(item.id)">阅读</el-button>
            </div>
          </el-collapse-item>
        </el-collapse>


      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Article',
    data() {
      return {
        article_title: '',
        activeNames: ['1']
      }
    },
    created() {},
    methods: {
      gotoDetails: function(id) {
        this.$router.push({
          path: '/details',
          query: {
            article_id: id
          }
        })
      },
      handleChange(val) {
        // console.log(val);
      }
    }
  }
</script>

<style scoped>
  .read-btn {
    padding-top: 20px;
  }

  .update-date{
    padding-top: 20px;
    color: #ccc;
  }
</style>



